function Verb(props) {

    // const [englishTranslation, setEnglishTranslation] = useState(false);
    // const handleClick = () => {
    //     setEnglishTranslation(!englishTranslation);
    // };
    //
    //
    // return (
    //     <li onClick={handleClick}>
    //         <span>
    //             {props.icelandic}
    //         </span>
    //         <span>
    //             {englishTranslation ?
    //                 props.english[0] :
    //                 props.russian[0]}
    //         </span>
    //     </li>
    // )

    let id;

    const [choice, setChoice] = useState(1);
    const handleClick = () => {
        clearTimeout(id);
        switch (choice) {
            case 1:
                setChoice(2);
                break;
            case 2:
                setChoice(3);
                break;
            default:
        }
        id = setTimeout(()=>setChoice(1),2000);
    };


    return (
        <li onClick={handleClick}>
            <span>
                {choice === 1 ?
                    props.icelandic :
                    choice === 2 ?
                        props.english[0] :
                        choice === 3 ?
                            props.russian[0] :
                            null}
            </span>
        </li>
    )
}

export default ListOfVerbs;